import { motion } from "framer-motion";
import { Link } from "react-router-dom";
import introTop from "../assets/intro-top.png";
import introFooter from "../assets/intro-footer.png";
import introFooterM from "../assets/intro-footer-m.png";
import Logo from "../assets/intro-logo.png";

export default function Intro() {
  return (
    <div className="home-intro">
      <motion.div
        className="home-intro-bg"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.5 }}
      ></motion.div>
      <motion.div
        className="intro-top"
        initial={{ opacity: 0, y: -300 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.5 }}
      >
        <Link to={"/home"}>
          <motion.img
            src={introTop}
            alt=""
            whileHover={{ scale: 1.1, zIndex: 2 }}
            whileTap={{ scale: 0.8 }}
            transition={{ type: "spring", stiffness: 400, damping: 10 }}
          />
        </Link>
      </motion.div>
      <div className="intro-center"></div>
      <motion.div
        className="intro-footer"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.5, delay: 0.3 }}
      >
        <img src={introFooter} alt="" className="hidden-m" />
        <img src={introFooterM} alt="" className="hidden-d" />
      </motion.div>
      <motion.div
        className="intro-footer-logo"
        initial={{ opacity: 0, y: 300 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.3 }}
      >
        <img src={Logo} alt="" />
      </motion.div>
    </div>
  );
}
